export default {
	name: 'uni-edit',
	props: {
		
		paddingBottom:{ 
			//当键盘弹起的时候，输入框距离工具栏的距离
			type: Number,
			default: 0
		},
		placeText:{
			//文本框默认的文字
			type: String,
			default: '写点什么吧...'
		},
		currentDate:{
			type: String,
			default: '1900-01-01'
		},
		//默认的文本类型
		contentData:{
			type:Object
		}
	},
	data() {
		return {
			pageHeight:'',
			editItems:[
				{
					type:'textarea',
					value:''
				}
			],
			
			locationCity:"北京",
			content:[]
		};
	},
	onLoad() {
		
		
	},
	methods:{
		submit:function(){
			
			return this;
		},
		/**
		 * 键盘输入时触发事件
		 */
		inputIng:function(e){
			var that = this;
			var value = e.detail.value;
			var index = e.currentTarget.dataset.index;
			that.$set(that.editItems[index],'value',value);
		},
		/**
		 * 输入框行数变化
		 * 当输入框行数变化时，动态赋值给整个content的一个高度，避免输入框跑到工具栏下边去
		 */
		linechage:function(e){
			//console.log(JSON.stringify(e));
			var that = this;
			var height = e.detail.height;
			var lineHeight = e.detail.lineHeight;
			var pageHeight = height + (lineHeight * 2) + uni.upx2px(98) + parseFloat(that.paddingBottom);
			this.pageHeight = pageHeight;
			
			uni.pageScrollTo({
				scrollTop: 10000000,
				duration: 0
			}); 
			
	
		},
		insertImg:function(){
		
			var that = this;
			uni.chooseImage({
				count: 1, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: function (res) {
					var temp = res.tempFilePaths;
					
						var result = that.editItems.push({
							type:'img',
							value:temp[0],
							progress:0
						})
											
						//图片上传至服务器
						var uploadTask = uni.uploadFile({
								url: 'http://demo.com/upload', //仅为示例，非真实的接口地址,需要替换成自己的接口地址
								filePath: temp[0],
								name: 'sendImg',
								success: (e) => {
									var data = JSON.parse(e.data);
									that.$set(that.editItems[result - 1],'value',data.pic_path);
								},
								complete: (e) => {
									// uni.hideLoading()
								} 
						});
						
						uploadTask.onProgressUpdate((res) => {
							console.log("上传进度" + res.progress);
							that.$set(that.editItems[result - 1],'progress',res.progress);
							// 测试条件，取消上传任务。
// 							if (res.progress > 50) {
// 								uploadTask.abort();
// 							}
						});
						
					
					
					
					that.editItems.push({
						type:'textarea',
						value:''
					})
					
					//plus.key.showSoftKeybord(); //显示软键盘
					
					uni.pageScrollTo({
						scrollTop: 10000000,
						duration: 0
					});
				}
			});
		},
		delImg:function(e){
			var index = e.currentTarget.dataset.index;
			var that = this;
			uni.showModal({
				title: '确定要删除该图片吗？',
				content: ' ',
				success: function (res) {
					if (res.confirm) {
						console.log('用户点击确定');
						that.editItems.splice(index,1); 
					} 
				} 
			});
			console.log(index);
		},
		chooseLocation:function(){
			uni.chooseLocation({
				success: function (res) {
					console.log('位置名称：' + res.name);
					console.log('详细地址：' + res.address);
					console.log('纬度：' + res.latitude);
					console.log('经度：' + res.longitude);
				}
			});
		}
	}
}; 
